html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  width: 100%;
  height: auto; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  font-style: normal;
  color: #666666;
  background-color: #e5e5e5; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  width: 100%;
  min-height: 856px;
  padding-bottom: 30px;
  color: #ffffff;
  background: url("../img/header-bg@1x.png") center no-repeat #1b2d37;
  overflow: hidden; }

.page-header__info {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 1454px;
  min-width: 1024px;
  width: 100%;
  padding: 33px 20px 0;
  margin: 0 auto 11.9%; }

.page-header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 357px;
  min-width: 280px;
  width: 26%; }

.page-header__nav-btn {
  width: 37px;
  height: 13px;
  background-color: transparent;
  border-top: 1px solid #ffffff;
  border-bottom: 1px solid #ffffff;
  border-left: none;
  border-right: none;
  opacity: 0.5; }

@media (max-width: 1023px) {
  .page-header {
    min-height: 656px; }
  .page-header__info {
    max-width: none;
    min-width: 320px;
    width: 100%;
    padding: 20px 25px 0; }
  .page-header__nav {
    align-items: flex-start;
    min-width: 83.3%;
    width: 83.3%; }
  .page-header__nav-btn {
    margin-top: 17px; } }

@media (max-width: 767px) {
  .page-header {
    min-height: 647px; }
  .page-header__info {
    padding: 17px 17px 0;
    margin-bottom: 207px; }
  .page-header__nav {
    min-width: 65.5%;
    width: 65.5%; }
  .page-header__nav-btn {
    margin-top: 10px; } }

.header-contacts {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 630px;
  width: 46.8%;
  padding-top: 5px; }

.header-contacts__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  width: 65%; }

.header-contacts__link {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 38px;
  line-height: 18px;
  color: #ffffff;
  text-decoration: none; }

.header-contacts__link::before {
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  content: "";
  background: center no-repeat; }

.header-contacts__link--mail::before {
  background-image: url("../img/mail.svg"); }

.header-contacts__link--phone::before {
  background-image: url("../img/phone.svg"); }

.header-contacts__btn {
  width: 183px;
  padding: 13px 12px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #ffffff;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px; }

@media (max-width: 1023px) {
  .header-contacts {
    min-width: 78.5px;
    width: 13.5%; }
  .header-contacts__wrapper {
    width: 100%; }
  .header-contacts__link::before {
    left: 12px;
    top: 5px;
    width: 27px;
    height: 27px; }
  .header-contacts__link span {
    font-size: 0; }
  .header-contacts__btn {
    display: none; } }

@media (max-width: 767px) {
  .header-contacts__link::before {
    left: 7px;
    top: -1px; }
  .header-contacts__link--phone::before {
    left: 14px; } }

.intro {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  max-width: 1461px;
  min-width: 1024px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto; }

.intro__title {
  width: 52%;
  margin: 0 0 26px 0;
  padding: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 5.5px;
  font-weight: 700;
  text-transform: uppercase; }

.intro__text {
  position: relative;
  width: 51%;
  padding: 0 0 0 94px;
  margin: 0 0 41px 0;
  font-size: 22px;
  line-height: 34px;
  text-transform: lowercase; }

.intro__text::before {
  position: absolute;
  left: 0;
  top: 15px;
  width: 66px;
  height: 2px;
  background-color: #ffffff;
  content: ""; }

.intro__btn {
  padding: 18px 45px;
  margin-left: 98px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.8px;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #00b2ff;
  border-radius: 5px;
  text-decoration: none; }

.intro__btn-short {
